@import url("https://fonts.googleapis.com/css2?family=Foldit:wght@700&family=Nabla&family=Playfair+Display&family=Roboto:ital,wght@1,100;1,300&family=Sansita+Swashed:wght@300&display=swap");

img {
  width: 100%;
  height: 190px;
}
.nav {
  width: 80%;
  display: grid;
  justify-content: space-between;
}
.nav li {
  margin-right: 2em;
}

* {
  box-sizing: border-box;
}

.cars {
  display: grid;
  grid-template-columns: 330px 330px 330px;

  margin-top: 3em;
  margin-left: 23em;
  grid-gap: 10px;
  margin-bottom: 1em;
}
h1 {
  text-align: center;
  font-size: 300%;
  font-family: "Sansita Swashed";
  border-top: 3px solid black;
}
h2 {
  text-align: center;
  font-size: 180%;
  font-family: cursive;
}
.header {
  width: 100%;

  padding: 2em;
}
.header img {
  height: 23em;
}
h3 {
  text-align: center;
  margin-top: 2em;
  text-decoration: underline;
  text-decoration-color: orange;
  letter-spacing: 2px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.car {
  margin-left: auto;
  margin-right: auto;
  width: 300px;
  height: 500px;
  border: 1px solid rgb(239, 228, 228);

  border-radius: 5px;
}
.car p {
  text-align: center;
}

.not {
  filter: grayscale(100%);
  opacity: 0.7;
}
.smallText {
  font-size: 12px;
}
.bigText {
  font-size: 18px;
  font-weight: bold;
}
.car:hover {
  background-color: rgb(231, 229, 229);
  border: 1px solid black;
  transform: scale(1.05);
}
.not:hover {
  transform: translateY(0px);
  border: none;
  background-color: rgb(243, 241, 241);
}
ul {
  display: flex;
  justify-content: space-between;
}
ul li {
  list-style: none;
  font-size: 1.7em;
  font-family: monospace;
}
.buyBtn {
  width: 40%;
  font-size: 130%;
  margin-left: 30%;
  border: none;
  background-color: rgb(172, 190, 206);
  margin-bottom: 5px;
  color: rgb(112, 81, 19);
  border-radius: 10px;
  cursor: pointer;
}
.buyBtn:hover {
  color: red;
  transform: scale(1.3);
}
.form {
  display: grid;
  width: 20em;
  height: fit-content;
  background-color: rgb(101, 142, 142);

  margin-left: auto;
  margin-right: auto;
}
.form label {
  margin-top: 2em;
  text-align: center;
  font-weight: bold;
}
.form input {
  height: 3em;
  background-color: aliceblue;
}
.submitBtn {
  margin-top: 2em;
  margin-bottom: 3em;
  width: 30%;
  margin-left: auto;
  margin-right: auto;
}
.rate {
  margin-left: 5em;
  font-size: large;
  margin-top: 2em;
}
.filter {
  display: grid;
  position: absolute;
  border: 1px solid rgb(211, 203, 203);
  padding-bottom: 2em;
  padding-right: 2em;

  border-top: none;
}

.filter li {
  margin-top: 2em;
  cursor: pointer;
  border-bottom: 1px solid black;
  font-size: 18px;
}
.filter li:hover {
  color: gray;
}
.arrow {
  color: brown;
  float: right;
  font-size: 2em;
  margin-left: 1.5em;
}
.filterP {
  text-align: center;
  font-weight: bold;
  font-size: 300%;
  border-bottom: none;
}
.electric {
  display: flex;
  width: 80%;
  background-color: rgb(27, 27, 182);
  justify-content: space-between;
  margin-left: 4em;
  margin-top: 4em;
  color: white;

  box-shadow: 1px 1px black;
  border-radius: 3px;
}
.electric img {
  width: 50%;
  height: 20em;
}
.electric p {
  font-size: 1em;
  text-align: center;
}

h4 {
  font-size: 3em;
  text-align: center;
}
.electric button {
  text-align: center;
  margin-left: 35%;
  font-size: 20px;
  border: 1px solid white;
  background-color: rgb(27, 27, 182);
  color: white;
  margin-top: 1em;
  padding: 15px;
}
.essentials {
  text-align: center;
  margin-top: 4em;
  font-size: 2em;

  letter-spacing: 5px;
}
.box {
  width: 18em;
  height: 15em;

  border: 1px solid rgb(183, 183, 183);
  padding: 10px;
  box-shadow: 1px 1px rgb(207, 206, 206);
}
.boxes {
  display: grid;
  grid-template-columns: 280px 280px 280px 280px;
  grid-gap: 4em;
  margin-left: 5%;
  margin-right: 10%;
  margin-top: 8%;
}
.box p {
  text-align: center;
  font-size: 1em;
  color: rgb(124, 117, 117);
  letter-spacing: 1px;
}
.sign {
  font-size: 3em;
}
.bold {
  font-weight: bold;
  color: rgb(69, 66, 66);
  font-size: 1.2em;
}
.model {
  width: 20em;
  height: 15em;
}
.evmodels {
  display: grid;
  grid-template-columns: 23em 23em 23em 23em;
  margin-left: 8px;
  margin-bottom: 2em;
}
.model p {
  text-align: center;
  font-weight: bold;
}
.model img {
  height: 70%;
  margin-top: 1em;
}
.discover {
  text-align: center;
  margin-top: 2em;
  margin-bottom: 2em;
  font-size: 2em;
}
.give1,
.give2,
.give3 {
  border: 1px solid black;
}
.give1 {
  width: 25%;
  height: 100%;
  font-size: 2.3em;
  text-align: center;
  background-color: black;
  color: white;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.give1 p {
  margin-top: 30%;
}
.give2 {
  width: 35em;
  height: 15em;
  padding: 10px;
  background-color: rgb(255, 251, 247);
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.give2 p {
  text-align: center;
  line-height: 2em;
}
.give2 span {
  font-size: 2.9em;
  line-height: 1em;
}
.give3 {
  width: 25em;
  height: 15em;
}
.give3 img {
  height: 100%;
}
.giveaway {
  display: flex;
  margin-left: 8%;
  height: 15em;
  width: 80%;
  margin-top: 8em;
}
.enterBtn {
  text-align: center;
  font-size: 120%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  background-color: rgb(55, 55, 216);
  color: white;
  border: none;
  padding-left: 28px;
  padding-right: 28px;
  padding-top: 5px;
  padding-bottom: 5px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.brands {
  display: grid;
  grid-template-columns: 320px 320px 320px 320px;
  margin-left: 7%;
}
.brands img {
  width: 20em;
  height: 15em;
  margin-bottom: 2em;
}
h5 {
  text-align: center;
  margin-top: 2em;
  margin-bottom: 2em;
  font-size: 1.8em;
}
.allbrand {
  text-align: center;
}
.showTesla {
  display: grid;
  grid-template-columns: 400px 500px;
  grid-gap: 100px;
  margin-left: 10%;
  border: 1px solid black;
  padding: 1em;
  width: fit-content;
}
.showTesla img {
  margin-top: auto;
  margin-bottom: auto;
}
.title {
  font-weight: bold;
}
